* {
  box-sizing: border-box;
}

.App {
  margin: 0;
  text-align: center;
  background-color: #282c34;
  font-family: 'Roboto', sans-serif;
}

.header {
  background-image: url("https://images.pexels.com/photos/1699020/pexels-photo-1699020.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  min-height: 20vh;
  display: grid;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  display: grid;
  grid-template-columns: 4fr 1fr;
  margin: 3em 2em 0em 2em;
  gap: 2em;
}

.wrapper{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 1em;
  margin: auto;
}

.cart {
  list-style-type: none;
  background-color:  rgba(255, 255, 255, 0.801);
  text-align: end;
}

.orderform{
  margin-top: 1em;
  border-top: 1px solid black;
  
}

.order{
  margin: 0.1em;
  border: 1px solid black;
  border-radius: 0.5em;
  padding: 0.5em;
}

.total {
  border-top: 1px solid black;
  padding-top: 1em;
}

.list{
  margin-right: 1em;
  padding: 1em;
}

.item {
  padding-bottom: 0.7em;
  padding-right: 0.4em;
  padding-left: 0.4em;
  border: 1px solid rgb(20, 20, 20);
  border-radius: 0.5em;
  background-color: rgba(255, 255, 255, 0.801);
}

.itemimg {
  border-radius: 0.5em;
  width: 20em;
  height: 20em;
}

.btn {
  background-color: #282c34;
  color: white;
  letter-spacing: 0.1em;
  padding: 0.3em;
  margin-left: 1em;
  border-radius: 0.3em;
}

.confirmbtn{
  margin-top: 1em;
  margin-left: 1em;
  background-color: rgb(0, 70, 0);
  color: white;
  letter-spacing: 0.1em;
  padding: 0.3em;
  border-radius: 0.3em;
}

.emptybtn{
  margin-top: 1em; 
  background-color: rgb(95, 0, 0);
  color: white;
  letter-spacing: 0.1em;
  padding: 0.3em;
  margin-left: 1em;
  border-radius: 0.3em;
}

.orderbtn{
  margin-top: 1em; 
  background-color: #282c34;
  color: white;
  letter-spacing: 0.1em;
  padding: 0.3em;
  margin-left: 1em;
  border-radius: 0.3em;
}

.amountbtn{
  border: 1px solid black;
  border-radius: 0.2em;
  width: 20%;
}

h3 {
  letter-spacing: 0.1em;
  font-family: 'Neucha', cursive;
}

h1 {
  letter-spacing: 0.1em;
  font-family: 'Rock Salt', cursive;
}

.amount {
  border-radius: 1em;
  cursor: pointer;
}
